.register {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.register__container {
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: #dcdcdc;
  padding: 30px;
}

.register__textBox {
  padding: 10px;
  font-size: 18px;
  margin-bottom: 10px;
}

.register__btn {
  padding: 10px;
  font-size: 18px;
  margin-bottom: 10px;
  border: none;
  color: white;
  background-color: #4caf50;
}

.register__google {
  background-color: #4285f4;
}

.register div {
  margin-top: 7px;
}

.navbar{
  background-image: linear-gradient(to right, #4285f4, #4285f4);
  box-shadow: 5px 10px 8px #88888845;
}
